export default {
	props: {
		item: Object,
		index: Number
	},
	methods: {
		handleEvent() {
			switch (this.item.clickType) {
				case 'navigateTo':
					if (this.item.url) {
						uni.navigateTo({
							url: this.item.url
						})
					}
					break;
				case 'clear':
					uni.showModal({
						title: '',
						content: '是否清除缓存？',							
						cancelText: '取消',
						confirmText: '清除缓存',
						success(res) {
							if (res.confirm) {
								uni.clearStorage()
								uni.showToast({
									title: '清除缓存成功'
								})
							}
						}
					})
					break;
			}
		}
	}
}
